import React from 'react'
import homepageCivic from '../assets/homepage.jpg'
import './homepage.css';
import { Link } from 'react-router-dom';
import { Box, 
         Card, 
         CardContent,
         CardActionArea,
         CardActions, 
         CardMedia,
         Grid,
         Typography } from '@mui/material'
         

         
function homepage() {
  return(
    <Box
      component="div"
      display="flex"
      marginTop="5px"
      justifyContent="center"
      backgroundColor="#fff"
      >
      <Box
        marginTop="10px"
        display="flex"
        justifyContent="center"
        width={'80%'}
        >
        <Grid container 
          spacing={4}
        >
          <Grid item xs={12} >
            <Typography variant="h4" color="#384145">
              <b>About Kanpeki Garage</b>
            </Typography>
            <Typography marginTop="12px" variant="body1">
            Kanpeki Garage was started as a passion project in 2022 to share our interest in cars and clothing. We aim to share quality media of cars, create a community that appreciate cars and produce quality clothing.
            </Typography>
            <img src={homepageCivic} className="homepagecivic nodrag" alt='homeimg' />
          </Grid>
          <Grid item xs={4} className="gallerydesk nodrag">
            <Card>
              <CardMedia
                component="img"
                height="350"
                image={require('../assets/gallery.jpg')}
                alt="Gallery"
              />
              <CardContent>
                <Typography gutterBottom variant="h5" component="div">
                  Gallery
                </Typography>
                <Typography variant="body1">
                  Our gallery contains collections of images of cars that we have shot to display every aspect of people's vechicles.
                </Typography>
              </CardContent>
              <div className="link-box" >
                <CardActions>
                  <Typography color="#3584e2" variant="body1" component={Link} to="/gallery">
                    <b>Click Here To Visit The Gallery</b>
                  </Typography>
                </CardActions>
              </div>
            </Card>
          </Grid>
          <Grid item xs={4} className="shopdesk nodrag">
            <Card>
              <CardMedia
                component="img"
                height="350"
                image={require('../assets/hoodiedesk.jpg')}
                alt="Shop"
              />
              <CardContent>
                <Typography gutterBottom variant="h5" component="div">
                  Shop
                </Typography>
                <Typography variant="body1">
                  Our shop contains high quality clothing, stickers and accessories. 
                </Typography>
              </CardContent>
              <div className="link-box" component={Link} to="/shop">
                <CardActions>
                  <Typography color="#3584e2" variant="body1" component={Link} to="/shop">
                    <b>Click Here To Visit The Shop</b>
                  </Typography>
                </CardActions>
              </div>
            </Card>
          </Grid>
          <Grid item xs={4} className="meetsdesk nodrag">
            <Card>
              <CardMedia
                component="img"
                height="350"
                image={require('../assets/banner.jpg')}
                alt="Meets"
              />
              <CardContent>
                <Typography gutterBottom variant="h5" component="div">
                  Photoshoot Request
                </Typography>
                <Typography variant="body1">
                  The photoshoot request contains a form you can fill out to get photos taken of your car or other.
                </Typography>
              </CardContent>
              <div className="link-box">
                <CardActions>
                  <Typography color="#3584e2" variant="body1" component={Link} to="/meets">
                    <b>Click Here To Book A Shoot </b>
                  </Typography>
                </CardActions>
              </div>
            </Card>
          </Grid>
          <Grid item xs={12} className="gallerymobile nodrag">
            <Card>
              <CardMedia
                component="img"
                height="350"
                image={require('../assets/gallery.jpg')}
                alt="Gallery"
              />
              <CardContent>
                <Typography gutterBottom variant="h5" component="div">
                  Gallery
                </Typography>
                <Typography variant="body1">
                  Our gallery contains collections of images of cars that we have shot to display every aspect of people's vechicles.
                </Typography>
              </CardContent>
              <div className="link-box" component={Link} to="/shop">
                <CardActions>
                  <Typography color="#3584e2" variant="body1" component={Link} to="/gallery">
                    <b>Click Here To Visit The Gallery</b>
                  </Typography>
                </CardActions>
              </div>
            </Card>
          </Grid>
          <Grid item xs={12} className="shopmobile nodrag">
            <Card>
              <CardMedia
                component="img"
                height="350"
                image={require('../assets/hoodie2.jpg')}
                alt="Shop"
              />
              <CardContent>
                <Typography gutterBottom variant="h5" component="div">
                  Shop
                </Typography>
                <Typography variant="body1">
                  Our shop contains high quality clothing, stickers and accessories. 
                </Typography>
              </CardContent>
              <div className="link-box" component={Link} to="/shop">
                <CardActions>
                  <Typography color="#3584e2" variant="body1" component={Link} to="/shop">
                    <b>Click Here To Visit The Shop</b>
                  </Typography>
                </CardActions>
              </div>
            </Card>
          </Grid>
          <Grid item xs={12} className="meetsmobile nodrag">
            <Card>
              <CardMedia
                component="img"
                height="350"
                image={require('../assets/banner.jpg')}
                alt="Meets"
              />
              <CardContent>
                <Typography gutterBottom variant="h5" component="div">
                  Photoshoot Request
                </Typography>
                <Typography variant="body1">
                  The photoshoot request contains a form you can fill out to get photos taken of your car or other.
                </Typography>
              </CardContent>
              <div className="link-box" component={Link} to="/shop">
                <CardActions>
                  <Typography color="#3584e2" variant="body1" component={Link} to="/meets" >
                    <b>Click Here To Book A Shoot </b>
                  </Typography>
                </CardActions>
              </div>
            </Card>
          </Grid>

        </Grid>
      </Box>
    </Box>
  ) 
    
  
}

export default homepage